@import './animation';
@import './reset';
@import './variables';

html,
body {
    min-width: 320px;
    font-family: $font-basic;
    font-size: $font-size-basic;
    font-weight: $font-weight-basic;
    background-color: $color-bg-main;
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 12px 0 12px;
    max-width: $container-width-base;
}

// MAIN

.grid-template {
    min-height: 490px;
    display: grid;
    grid-template-columns: minmax(auto, 492px) minmax(400px, auto);
    grid-template-rows: auto 1fr;

    &__col-2 {
        grid-row-start: span 2;
    }

    @media (max-width: 992px) {
        min-height: auto;
        grid-template-columns: 1fr;
    }
}

.main {
    padding-top: 12px;

    @media (max-width: 576px) {
        padding-top: 0px;
    }

    &__container {
        @media (max-width: 576px) {
            padding: 0;
        }
    }

    &__inner {
        background-color: $color-bg-gray;
        padding: 40px 40px 40px 40px;

        @media (max-width: 992px) {
            flex-direction: column;
        }

        @media (max-width: 576px) {
            padding: 20px;
        }
    }

    &__outher {
        position: relative;
    }

    &__app {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.title {
    font-size: 48px;
    line-height: 52px;
    font-weight: 400;

    @media (max-width: 992px) {
        font-size: 36px;
        line-height: 36px;
        padding-bottom: 15px;
    }
}

.title_h2 {
    font-size: 48px;
    line-height: 52px;
    font-weight: 400;

    @media (max-width: 992px) {
        font-size: 36px;
    }
}

.title-after {
    padding: 10px 0 16px 0;
    line-height: 32px;
    display: block;
}

.agreement-text {
    position: absolute;
    bottom: 0;
    left: 0;
    font-size: 12px;
    line-height: 16px;
    display: block;

    & > a {
        font-weight: 400;
    }

    @media (max-width: 992px) {
        position: relative;
    }
}

// MAIN END

.main__app > * {
    width: 100%;
    max-width: 620px;

    @media (max-width: 992px) {
        max-width: 100%;
    }
}

// CONTENT BLOCK
.content-block {
    &__dark {
        color: $font-color-white;
    }

    &__inner {
        padding: 120px 40px 0px 40px;

        &_padding {
            padding-bottom: 120px;
        }

        @media (max-width: 576px) {
            padding: 40px 0 80px 0;
        }
    }
}

.article-default {
    display: flex;

    &__text {
        @media (max-width: 992px) {
            padding-bottom: 40px;
        }
    }

    &__images {
        padding-left: 3%;
    }

    & > * {
        flex: 0 0 auto;
        width: 50%;
    }

    @media (max-width: 992px) {
        flex-wrap: wrap;

        & > * {
            width: 100%;
        }
    }
}

.complex-view {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 992px) {
        max-width: 70%;
        margin: 0 auto;
    }

    @media (max-width: 576px) {
        max-width: 100%;
    }

    &__border-element {
        flex: 1 0 auto;
        width: auto;
        height: calc(100% - 112px);
        position: relative;

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            transform: translate(0%, -50%);
            left: 0;
            width: 70%;
            height: 1px;
            border-top: 1px dashed #919e9f;
        }

        &:after {
            content: '';
            position: absolute;
            width: 30%;
            height: 100%;
            border-top: 1px dashed #919e9f;
            border-bottom: 1px dashed #919e9f;
            border-left: 1px dashed #919e9f;
            right: 0;
        }

        @media (max-width: 576px) {
            height: calc(100% - 49px);
        }
    }

    &__item {
        transition: all 0.3s;
        width: 112px;
        height: 112px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 13px;
        background-color: #323033;

        &_orange {
            background-color: $color-orange;
            width: 92px;
            height: 92px;
        }

        @media (max-width: 576px) {
            width: 49px;
            height: 49px;
            padding: 5px;
        }
    }

    &__right-column {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    @media (max-width: 992px) {
        &__item:last-child {
            margin-top: 70px;
        }
    }
}

.text-default {
    font-size: 24px;
    font-weight: 300;
    line-height: 32px;
    color: #eee;
}

.article-default__text .text-default {
    margin-top: 40px;
}
// CONTENT BLOCK END

// TABS
.custom-tab {
    &__content {
        display: none;

        &_active {
            display: block;
        }
    }

    &__controls {
        margin: 44px 0;
        list-style-type: none;
        display: flex;
        font-weight: 400;
        color: #636367;
        align-items: flex-end;
    }

    &__control {
        user-select: none;
        padding-bottom: 3px;
        line-height: 32px;
        margin-right: 15px;
        transition: all 0.3s;

        @media (max-width: 576px) {
            flex: 0 1 50%;
        }

        &_active {
            color: $font-color-white;
            border-bottom: 1px solid #eeeeee;
        }

        &:hover {
            color: $font-color-white;
            cursor: pointer;
        }
    }
}
// TABS END

// TABLE
.technical-table {
    &__row {
        display: flex;
        padding: 12px 0;
        border-bottom: 1px solid #2d2c2e;

        @media (max-width: 576px) {
            flex-wrap: wrap;
        }

        & > * {
            flex: 0 1 auto;

            &:first-child {
                @media (max-width: 576px) {
                    margin-bottom: 15px;
                }
            }
        }
    }

    &__item:first-child {
        width: 40%;

        @media (max-width: 576px) {
            width: 100%;
        }
    }
}

.custom-table {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    column-gap: 2%;

    > * {
        flex: 0 0 auto;
        width: 49%;

        @media (max-width: 992px) {
            width: 100%;
        }
    }

    @media (max-width: 576px) {
        column-gap: 0;
    }
}
// TABLE END
